$background-color: #f1faee;
$primary-color: #1d3557;
$secondary-color: #457b9d;
$tertiary-color: #80d8da;
$other-color: #e63946;
$color-black: #13242C;
$color-white: #fff;
$color-red-1: #D13433;
$color-red-2: #932422;
$color-skin-1: #EAB08C;
$color-skin-2: #D9766C;
$color-skin-3: #B2524D;
$color-hair: #13242C;
$color-wood-1: #601513;
$color-wood-2: #410A09;

* {
    box-sizing: border-box;
    outline: none;
    text-decoration: none;
    color: #edf6f9;
    margin: 0;
    padding: 0;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    line-height: 1rem;
    scroll-behavior: smooth;

    &::-webkit-scrollbar {
        display: none;
    }
}


@media screen and (min-width: 768px) {
    body {
        background: black;
        overflow-x: hidden;
        // overflow: hidden;

        .navigation-container {
            background: rgba(white, .15);
            backdrop-filter: blur(.2rem);
            position: absolute;
            top: 5px;
            left: 50%;
            translate: -50% 0;
            width: calc(100% - 10px);
            height: 4.5rem;
            display: flex;
            flex-direction: row;
            align-items: center;
            padding: .75rem;
            justify-content: space-evenly;
            border-radius: .3rem;
            font-size: .9rem;
            font-weight: 500;
            z-index: 4;

            .logo-container {
                width: 10rem;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                color: white;
                cursor: pointer;

                .ico {
                    width: 2rem;
                    height: 2rem;
                    border-radius: 58% 42% 58% 42% / 50% 49% 51% 50%;
                    background: $tertiary-color;
                    margin-right: .75rem;
                }

                .logo {
                    height: 100%;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    line-height: 2rem;

                    .text {
                        font-weight: 700;
                        font-size: 2rem;
                        cursor: pointer;
                        line-height: 1.5rem;
                    }
                }
            }

            .navigation-wrapper {
                width: calc(90% - 10rem);
                height: 100%;
                // background: teal;
                display: flex;
                align-items: center;
                justify-content: flex-end;

                .navigation {
                    display: flex;
                    align-items: center;
                    justify-content: space-evenly;
                    // background: goldenrod;
                    padding: 0 .8rem;
                    width: 70%;
                    height: 100%;

                    a {
                        color: white;
                        position: relative;
                        text-transform: uppercase;
                        font-size: .95rem;

                        &::after {
                            content: '';
                            position: absolute;
                            height: 2px;
                            width: 0;
                            left: 50%;
                            translate: -50% 0;
                            bottom: -2px;
                            background: white;
                            transition: .3s ease;
                        }

                        &:hover::after {
                            width: 100%;
                        }
                    }
                }
            }
        }

